	module.exports = {"data":{"markdownRemark":{"html":"<p>The framework <a href=\"https://vuejs.org/\">Vue</a> has constantly been flashing in my radar as a new shiny thing to learn.\nI was still busy upping my React game and had no opportunity to learn new stuff (read: I'm a fucking slob).\nBut as a resolution of sorts I want to commit code at least once a day. This sets up a good excuse to learn Vue!</p>\n<p>Vue was created by this dude <a href=\"https://github.com/yyx990803\">Evan You</a>, who at the time was working for Google and Angular.\nHe pulled out what he liked about Angular, added in a virtual DOM, stripped it down to remove all the cruft he wouldn't\nneed, and made it really lightweight. My first impression was that it really reminds me of the Angular of yore.\nThe prefixed directives, the two-way binding, models, and controllers. Man, what a feels trip!</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token operator\">&lt;</span>div id<span class=\"token operator\">=</span><span class=\"token string\">\"app\"</span><span class=\"token operator\">></span>\n  <span class=\"token punctuation\">{</span><span class=\"token punctuation\">{</span> message <span class=\"token punctuation\">}</span><span class=\"token punctuation\">}</span>\n<span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>div<span class=\"token operator\">></span>\n</code></pre>\n      </div>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token keyword\">var</span> app <span class=\"token operator\">=</span> <span class=\"token keyword\">new</span> <span class=\"token class-name\">Vue</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span>\n  el<span class=\"token punctuation\">:</span> <span class=\"token string\">'#app'</span><span class=\"token punctuation\">,</span>\n  data<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n    message<span class=\"token punctuation\">:</span> <span class=\"token string\">'Hello Vue!'</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n</code></pre>\n      </div>\n<p><em>Actual \"Hello, world!\" example from the <a href=\"https://vuejs.org/v2/guide/\">docs</a></em></p>","frontmatter":{"title":"Rewriting jkbx from Angular 1.x to Vue","date":"15 February, 2018","tags":["software-development","vue","angular","personal-projects"]}}},"pathContext":{"slug":"/rewriting-jkbx/"}}

